.services {
  padding-top: 6rem;
  padding-bottom: $gap-v--section;


  &__title {
  }

  &__descr {
  }

  &__grid-cards {
    @include row();
    margin-top: -6px;
  }

  &__card {
    @include col-ready();
    @include col(3);
    margin-top: 56px;
    text-align: center;
  }

  &__card-inner {
    border-radius: $border-radius--big;
    overflow: hidden;
    background-color: $pink-light;
    padding-bottom: 20px;
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  &__pic {

  }

  &__pic img {
    width: 100%;
    height: auto;
    max-height: 250px;
    object-fit: cover;
    object-position: 50% 0%;
    transform-origin: 50% 100%;
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.1);
    }
  }

  &__card-title {
    font-size: 1.8rem;
    letter-spacing: 0.03em;
    margin-top: 0.2em;
    padding-left: 1em;
    padding-right: 1em;
  }

  &__card-title a {
    color: $text-color;
    text-decoration: none;

    &:hover {
      color: $blue;
      text-decoration: underline;
    }
  }

  &__card-link {
    margin-top: auto; //0;
  }

  &__btn-wrap {
    margin-top: 55px;
    text-align: center;
  }

  &__btn {

  }


  /* MQ
  ========================================= */
  @at-root {
    /* md- */
    @include media-breakpoint-down(md) {
      &__card {
        @include col(4);
      }

      &__pic img:hover {
        transform: none;
      }
    } // End /md

    /* sm- */
    @include media-breakpoint-down(sm) {
      &__card {
        @include col(6);
      }
    } // End /sm

    /* MEDIA < 480px */
    @media (max-width: 480px) {
      &__card {
        @include col(10,1);
      }
    }
  } // End @at-root
}
