/* INPUT & TEXTAREA
========================================= */
.field-text {
  $placeholder-color: #9fa3a7;
  $opacity-disabled: 0.3;

  display: block;
  position: relative;
  margin: 0;
  padding: 0;

  & + & {
    margin-top: $fields-between-margin-vertical;
  }

  &__name {
    display: block;
    text-transform: uppercase;
  }

  &__input-wrap {
    display: block;
    margin-top: 1.5rem;
  }

  &__input {
    font-family: $font-family--system; // inherit
    font-weight: normal;
    display: inline-block;
    vertical-align: middle;
    width: 100%;
    max-width: 100vw;
    padding: 1.1rem  $field-padding-horizontal--input;
    font-size: $font-size;
    line-height: $line-height;
    margin-bottom: 0;
    box-sizing: border-box; //чтобы поле не вываливалось из контейнера
    border: 1px solid rgba($black, 0.2);
    border-radius: 23.5rem;
    color: $black;

    &::placeholder {
      opacity: 1;
      color: $placeholder-color !important; // stylelint-disable-line
      font-size: 1.4rem;
      font-style: normal;
    }

    &:focus {
      @include field-focus;
    }

    &[disabled],
    &[readonly] {
      cursor: default;
      opacity: $opacity-disabled;
    }

    &[type='search']::-webkit-search-cancel-button,
    &[type='search']::-webkit-search-decoration {
      -webkit-appearance: none; // stylelint-disable-line  property-no-vendor-prefix
    }

    &[type='number']::-webkit-inner-spin-button,
    &[type='number']::-webkit-outer-spin-button {
      height: auto;
    }

    &[type='color'] {
      max-width: 100vw;
      padding: 0;
    }

    &__textarea {
      height: auto;
      resize: vertical;
      // min-height: *px;
      //чтоб избежаеть нижнего отступа, кот. появляется из-за inline-block
      vertical-align: top; // Альтернатива — display: block;
    }
  }

  &__help-text {
    @include field-help-text;
  }

  &__flag-required {
    color: red;
  }


  /* ПРОЕКТНЫЕ
  ========================================= */
  &__icon {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      width: 39px;
      height: 39px;
      top: 50%;
      transform: translateY(-50%);
      right: 5px;
      border-radius: 50%;
      background-color: #ffc80a;
      background-repeat: no-repeat;
      background-position: 50%;
      background-size: auto 20px;
    }

    & input {
      padding-right: 50px;
    }

    &--tel::before {
      background-image: url('../img/i_phone.svg');
    }

    &--email::before {
      background-image: url('../img/i_form-envelope@1x.svg');
      background-size: 20px auto;
    }

    &--name::before {
      background-image: url('../img/i_user.svg');
    }
  }
}

