.btn {
  $block: &;

  $bg: #ffbd35;
  $color: $purple;
  $padding-v: $field-padding-vertical;
  $padding-h: 5rem; // $field-padding-horizontal;
  $font-size: 2.6rem;
  $text-transform: uppercase;
  $border-radius: 34.5px;

  display: inline-block;
  vertical-align: middle;
  box-sizing: border-box;
  margin: 0;
  padding: $padding-v $padding-h;
  font-family: inherit;
  font-size: $font-size;
  font-style: italic;
  font-weight: bold;
  line-height: 1.2;
  text-align: center;
  color: $color;
  text-transform: $text-transform;
  text-decoration: none;
  letter-spacing: 0.03em;
  white-space: normal;
  background-color: $bg;
  background-image: $gradient--btn;
  border: none;
  border-radius: $border-radius;
  cursor: pointer;
  user-select: none;
  box-shadow: 0 3px 0 0 rgba(207, 156, 46, 1);


  &:hover {
    text-decoration: none;
    background-color: darken($bg, 10%);
    color: $color;
    box-shadow: 0 3px 0 0 rgba(207, 156, 46, 1), $shadow;
    background-image:
      linear-gradient(rgba(#feea40, 0.5), rgba(#feea40, 0.5)),
      $gradient--btn;
  }

  &:active {
    // @include field-focus;
    color: rgba($color, 0.8);
    box-shadow: 0 0 15px rgba(207, 156, 46, 1) inset;
    outline: 0;
  }

  &:focus {
    outline: 0;
  }


  /* MQ
  ========================================= */
  @at-root {
    /* sm- */
    @include media-breakpoint-down(sm) {
      & {
        @include hyphens();
      }
    } // End /sm

    /* MEDIA <400px */
    @media (max-width: 400px) {
      & {
        font-size: 2rem;
        padding-left: 3rem;
        padding-right: 3rem;
      }
    }
  } // End @at-root


  &--disabled,
  &:disabled {
    background-color: $gray-lightest;
    border-color: $gray-light;
    cursor: not-allowed;
    opacity: $opacity-disabled;

    @at-root a.btn--disabled {
      pointer-events: none;
    }
  }


  &--w100 {
    width: 100%;
  }


  &--ghost {
    display: inline-block;
    vertical-align: middle;
    margin-top: 3.5rem;
    margin-right: auto;
    margin-left: auto;
    padding: 1rem 2rem;
    font-size: 1.8rem;
    line-height: 1;
    color: $text-color;
    background-color: #fff;
    text-decoration: none;
    font-style: italic;
    border: 2px dashed $text-color;
    border-radius: 10px;
    transition: all 0.2s;
    box-sizing: inherit;
    font-weight: normal;
    text-transform: none;
    letter-spacing: auto;
    background-image: none;
    box-shadow: none;

    &:hover {
      text-decoration: none;
      background-color: $yellow;
      color: $purple;
    }
  }


  // &__icon {
  //   display: inline-block;
  //   vertical-align: middle;
  // }
}
