.programs {
  padding-top: $gap-v--section * 2;
  padding-bottom: $gap-v--section;

  &__title {
  }

  &__grid {
    @include row();
  }

  &__descr {
    @include col-ready();
    @include col(10,1);
  }

  &__column {
    margin-top: 4.3rem;
    @include col-ready();
    @include col();
  }


  &__slider {
  }

  &__program {
    @include row();
  }

  &__program-info {
    @include col-ready();
    @include col(5);
    margin-left: $grid-gutter-width / 2;
    margin-right: ($grid-gutter-width / 2 * -1);
  }

  &__slide-pic {
    @include col-ready();
    @include col(7);
    padding-top: 5px;
  }

  &__program-title a {
    color: $text-color;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
      color: $blue;
    }
  }

  &__program-descr {
  }


  &__program-params-outer {
    width: 60%;
    text-align: center;
    padding-bottom: 20px;
  }

  &__program-params {
    display: flex;
    @include reset-ul();
    @include ie-li-nobullets();
    margin-top: 6.4rem;

    & > * {
      padding-left: $grid-gutter-width / 2;
      padding-right: $grid-gutter-width / 2;
      line-height: 1;
      text-align: center;
    }
  }

  &__program-params b {
    font-size: 3rem;
    font-weight: bold;
    font-style: italic;
  }

  &__program-link {
  }

  &__slide-pic img {
    width: 682px; //100%;
    height: 404px; //auto;
    border-radius: $border-radius--big;
    overflow: hidden;
    object-fit: cover;
    object-position: 50% 0%;
  }

  &__thumbs-wrap {
    position: relative;
    left: #{$grid-gutter-width * -0.5};
    width: calc(100% + #{$grid-gutter-width * 1});
    overflow: hidden;
  }

  &__thumbs {
    position: relative;
    margin-top: 15px;

    &::before {
      content: '';
      position: absolute;
      z-index: 2;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-image:
        linear-gradient(to left, rgba($white, 0.8) 0, transparent 211px),
        linear-gradient(to right, rgba($white, 0.8) 0, transparent 211px);
      background-position: left top, right top;
      user-select: none;
      pointer-events: none;
    }
  }

  &__thumbs-slider {
    display: flex;
  }

  &__thumbs-slide {
    padding-top: 9px;
    padding-left: 8px;
    padding-right: 8px;
    margin-left: 13px;
    margin-right: 13px;
    border-radius: $border-radius--big;

    &:active,
    &:focus {
      background-color: rgba($yellow, 0.4);
      outline: none;
    }

    &:hover { cursor: pointer; }
  }

  &__thumbs-pic img {
    width: 263px;
    height: 156px;
    object-fit: cover;
    object-position: 50% 50%;
    overflow: hidden;
    border-radius: $border-radius--big;
  }

  &__thumbs-title {
    font-weight: bold;
    font-style: italic;
    font-size: 1.8rem;
    letter-spacing: 0.02em;
    line-height: 1.2;
    padding-top: 1rem;
    padding-bottom: 2.5rem;
  }

  &__btn-outer {
    text-align: center;
    margin-top: 3rem;
  }


  /* MQ
  ========================================= */
  @at-root {
    /* HD > 1818px */
    @media (min-width: #{map-get($container-max-widths, xl) + 612}) {
      &__thumbs-wrap {
        left: -306px;
        width: calc(100% + 612px);
      }
    }

    /* sm- */
    @include media-breakpoint-down(sm) {
      & {
        padding-top: $gap-v--mob;
      }

      &__thumbs-wrap { display: none; }

      &__descr {
        @include col(12,0);
      }

      &__slide-pic {
        @include col(12);
        order: -1;
      }

      &__program-info {
        $picH: 404px;
        @include col(12);
        text-align: center;
        position: relative;
        margin-top: ($picH / 3 * -1);
        margin-left: 0;
        margin-right: 0;
        z-index: 1;
        min-height: ($picH / 3);
        overflow: hidden;
        background-image: linear-gradient(to bottom, rgba(#fff, 0.8) 30%, #e9dfd7 70% );
        text-shadow: 2px 1px 0 #fff, 2px 2px 5px #fff;
      }

      &__program-info li {
        padding-right: 0;
        padding-left: 0;

        &:not(:first-child) {
          padding-left: $grid-gutter-width / 2;
        }
      }

      &__program-descr {
        display: none;
      }

      &__program-params-outer {
        width: 100%;
      }

      &__program-params {
        margin-top: 0;
        justify-content: center;
      }

      &__program-params li {
        padding-left: 0;
      }

      &__program-params b {
        font-size: 2rem;
      }

      &__program-link {
        margin-top: 1rem;
      }
    } // End /sm

    /* MEDIA <575px */
    @media (min-width: 440px) and (max-width: 575px) {
      & {
        padding-top: $gap-v--mob * 2;
      }
    }

    /* xs */
    @include media-breakpoint-only(xs) {
      &__btn-outer {
        margin-top: $gap-v;
      }
    } // End /xs
  } // End @at-root
}
