// Кнопка «Наверх»
.go-top__btn {
  $size: 40px;

  position: fixed;
  z-index: ($z-index-modal - 2);
  right: 20px;
  bottom: 20px;
  display: none;
  width: $size;
  height: $size;
  border-radius: 50%;
  background: #000 url('data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20viewBox%3D%220%200%20448%20512%22%20fill%3D%22white%22%3E%3Cpath%20d%3D%22M240.971%20130.524l194.343%20194.343c9.373%209.373%209.373%2024.569%200%2033.941l-22.667%2022.667c-9.357%209.357-24.522%209.375-33.901.04L224%20227.495%2069.255%20381.516c-9.379%209.335-24.544%209.317-33.901-.04l-22.667-22.667c-9.373-9.373-9.373-24.569%200-33.941L207.03%20130.525c9.372-9.373%2024.568-9.373%2033.941-.001z%22/%3E%3C/svg%3E') no-repeat center 40%;
  background-size: ($size - 20) auto;
  opacity: 0.3;

  &:hover {
    cursor: pointer;
    opacity: 1;
  }

  /* MQ */
  @at-root {
    /* md- */
    @include media-breakpoint-down(md) {
      &,
      &:focus,
      &:active,
      &:hover {
        opacity: 0.3;
      }
    } // End /md
  } // End @at-root
}
