.footer {
  position: relative;
  padding: 0;
  overflow: hidden;
  text-align: center;
  background-color: #f9e7f3;
  background-image: url('../img/bg_footer.svg'), url('../img/bg_waves_top-white_bottom-pink.svg');
  background-repeat: no-repeat, repeat-x;
  background-position: 50% 40px, -440px -1px;
  background-size: 110% auto, 240% auto;

  &__container {
    padding-top: 238px;
    padding-bottom: 135px;
  }

  &__grid {
    @include row();
    align-items: stretch; //flex-start
    margin-top: 2rem;
  }

  &__contacts {
    @include col-ready();
    @include col(5,1);
    position: relative;
    padding: 8.5rem;
    padding-top: 7.5rem;
    font-weight: normal;
    line-height: 1.7;
    font-family: $font-family--system;
    text-align: left;
    font-style: normal;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: calc(100% - 27px);
      height: 100%;
      border: 1px dashed $purple;
      border-radius: $border-radius--big;
    }
  }

  &__contactform {
    @include col-ready();
    @include col(5);
  }

  &__contactform-inner {
    position: relative;
    width: 100%;
    margin-left: $grid-gutter-width / 2;
    padding: 4rem 5.5rem 5rem;
    text-align: center;
    color: #ffffff;
    background: #c167a4 linear-gradient(to right, #ca68a7, #a0629b) no-repeat 0 0;
    border-radius: $border-radius--big;
  }

  &__tel {
    font-size: 3.6rem;
  }

  &__email {
    font-size: 2.3rem;
  }

  &__contacts-group {
    font-weight: bold;
    line-height: 1.1;
    font-family: $font-family;
  }

  &__contacts-group a {
    text-decoration: none;
  }

  &__address {
    margin-top: 3.1rem;
  }

  &__privacy {
    margin-top: 7.2rem;
    font-weight: normal;
    font-family: $font-family;
    font-style: italic;
  }

  &__form-title {
    margin-top: 0;
    margin-bottom: 0.5em;
    font-size: 3.6rem;
  }


  &__girl {
    position: absolute;
    bottom: 0;
    background-repeat: no-repeat;
    background-size: auto 100%;

    &--left {
      left: -160px;
      width: 369px;
      height: 659px;
      max-height: 659px;
      background-image: url('../img/bg_footer__barboskiny-rozy@1x.png');
      background-position: left bottom;
    }

    &--right {
      right: -360px;
      width: 516px;
      height: 657px;
      max-height: 657px;
      background-image: url('../img/bg_footer__elza@1x.png');
      background-position: right bottom;
    }
  }


  /* MQ
  ========================================= */
  @at-root {
     /* MEDIA <1410px */
    @media (max-width: 1410px) {
      &__girl--left {
        bottom: -20%;
        left: -30px;
        height: 50%;
      }

      &__girl--right {
        display: none;
      }
    } // End /lg

    /* lg- */
    @include media-breakpoint-down(lg) {
      & {
        background-size: 100% auto;
      }

      &__contacts {
        padding-right: $grid-gutter-width;
        padding-left: $grid-gutter-width * 1.5;
      }

      &__girl--left {
        left: -90px;
        width: 369px;
        height: 500px;
      }
    } // End /lg

    /* md- */
    @include media-breakpoint-down(md) {
      &__container {
        padding-top: $gap-v--section;
        padding-bottom: $gap-v--section;
      }

      &__contacts {
        @include col(10,1);
        padding-right: $grid-gutter-width;
        padding-left: $grid-gutter-width;
        text-align: center;

        &::before {
          width: 100%;
        }
      }

      &__contactform {
        @include col(10,1);
        margin-top: $gap-v;
        padding-right: 0;
        padding-left: 0;
      }

      &__contactform-inner {
        margin-left: 0;
        padding-top: 7.3rem;
        padding-bottom: 7.3rem;
      }

      &__privacy {
        display: none;
      }

      &__girl--left {
        top: 120px;
        left: 130px;
        height: 50%;
        background-size: auto 80%;
        transform: rotate(45deg);
        transform-origin: 0 0;
      }

      &__girl--right {
        display: none;
      }
    } // End /md

    /* xs */
    @include media-breakpoint-only(xs) {
      &__container {
        padding-bottom: $gap-v;
      }

      &__contacts,
      &__contactform {
        @include col(12,0);
      }

      .footer__contactform-inner {
        padding-right: 1rem;
        padding-left: 1rem;
      }

      &__tel {
        font-size: 3rem;
      }

      &__email {
        font-size: 1.8rem;
      }

      &__girl--left {
        display: none;
      }
    } // End /xs
  } // End @at-root
}
