.slider {
  $block: &;

  $w: null;
  $h: null;
  // Arrows
  $arrow-w: 46px;
  $arrow-h: 46px;
  $arrow--prev: '../img/i_arrow-left@1x.svg';
  $arrow--next: '../img/i_arrow-right@1x.svg';
  // Dots
  $place-for-dots: null; //70px;
  $dot-size: 19px;


  //////////////////////////
  @if ($w) { width: $w; }
  @if ($h) { height: $h; }
  // overflow: hidden;
  @if ($place-for-dots) { padding-bottom: $place-for-dots; }


  // При загрузке показываем только первый слайд (чтоб избежать скачков)
  &__slide:not(:first-child) {
    display: none;

    .slick-initialized & {
      display: block;
    }
  }

  &__slide:focus {
    outline: none;
  }

  &__arrow {
    position: absolute;
    @if ($place-for-dots) { top: calc(50% + 5px - (#{$place-for-dots} / 2)); }
    @else { top: 50%; }
    z-index: 1;
    width: $arrow-w;
    height: $arrow-h;
    border: none;
    padding: 0 !important;
    background-color: $yellow;
    background-position: 50%;
    background-repeat: no-repeat;
    transform: translateY(-89%); // поправка на тень

    &:focus {
      outline: none;
    }

    &--prev {
      left: ($grid-gutter-width / 2 * -1); // Последний делитель — прозапас
      // left: 0;
      background-image: url($arrow--prev), $gradient--btn;

      &:hover {
        background-image:
          url($arrow--prev),
          linear-gradient(rgba(#feea40, 0.5), rgba(#feea40, 0.5)),
          $gradient--btn;
      }
    }

    &--next {
      right: ($grid-gutter-width / 2 * -1); // Последний делитель — прозапас
      // right: 0;
      background-image: url($arrow--next), $gradient--btn;

      &:hover {
        background-image:
          url($arrow--next),
          linear-gradient(rgba(#feea40, 0.5), rgba(#feea40, 0.5)),
          $gradient--btn;
      }
    }


    /* MQ
    ========================================= */
    @at-root {
      /* HD 1370px+ */
      @media (min-width: #{map-get($container-max-widths, xl) + 160 + 30}) {
        &--prev {
          left: (-35px - $arrow-w);
        }

        &--next {
          right: (-35px - $arrow-w);
        }
      }
    } // End @at-root


    /* El.MODIFICATOR: --js-position
    ========================================= */
    &--js-position.slider__arrow--prev {
      left: (-35px - $arrow-w);
    }

    &--js-position.slider__arrow--next {
      right: (-35px - $arrow-w);
    }
  }


  &__dot {
    width: $dot-size;
    height: $dot-size;
    padding: 0;
    border: 1px solid rgba(#3d3d3e, 0.15);
    border-radius: 50%;
    background-color: $white;
    transition: all 0.3s;

    &:focus { outline: none; }

    .slick-active & {
      // border-color: $black;
      // background-color: $black;
      background-image: $gradient--btn;
      box-shadow: 0 2px 0 0 rgba(207, 156, 46, 1);
    }
  }
}


.slick {
  &-dots {
    @include reset-ul();
    position: static;
    text-align: center;
    padding-top: 2rem;
    margin-left: auto;
    margin-right: auto;

    @include media-breakpoint-down(sm) {
      padding-top: ($line-height--fix / 2);
    }
  }

  &-dots li {
    display: inline-block;
    vertical-align: middle;

    &:not(:first-child) {
      margin-left: 40px;
    }
  }

  [class*=thumbs] &-current {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      width: calc(100% + 4px);
      height: 100%;
      top: 0;
      left: -2px;
      border: 2px dashed #f3adda;
      border-radius: inherit;
      max-width: 283px; // 263 + 8*2 + 2*2
    }
  }
}

