.animators {
  padding-top: 6px;
  padding-bottom: $gap-v--section;

  &__slider {
    @include row();
    flex-shrink: 0;
  }

  &__slide {
    @include col-ready();
    @include col(4);

    &:not(:first-child) {
      display: block;
    }
  }

  /* Нивилирует отступ после последнего слайда, добавляя отступ первому */
  & .slick-list {
    margin-left: 34px;
  }

  &__slide img {
    width: 325px;
    height: 723px; // 90vh;
    border-radius: 25px;
    object-fit: cover;
    object-position: 50% 0;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  &__name {
    margin-top: 1.7em;
    font-size: 1.8rem;
    text-align: center;
    letter-spacing: 0.03em;
  }

  &__name a {
    color: $text-color;
    text-decoration: none;

    &:hover {
      color: $blue;
      text-decoration: underline;
    }
  }

  // HardCode (Gonflict styles: grid & slick)
  & .slider__arrow--prev {
    left: 0 !important;
  }

  & .slider__arrow--next {
    right: 0 !important;
  }


  /* MQ
  ========================================= */
  @at-root {
    /* lg- */
    @include media-breakpoint-down(lg) {
      & .slick-list {
        margin-left: 0;
      }

      &__slide img {
        width: 100%;
        height: auto;
      }
    } // End /lg

    /* sm- */
    @include media-breakpoint-down(sm) {
      &__slide {
        @include col(6);
      }

      &__slide img {
        max-height: 90vh;
        max-width: 325px;
        min-width: 226px;
      }
    } // End /sm

    /* xs */
    @include media-breakpoint-only(xs) {
      &__slide {
        @include col(12);
      }

      &__slide img {
        max-height: 50vh;
      }
    } // End /xs
  } // End @at-root
}
