.field-actions {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: flex-start;
  margin-top: 3.4rem;


  &__text {
    display: block;
    margin-top: 1.8rem;
    margin-bottom: 1rem;
    margin-left: auto;
    margin-right: auto;
    color: #f1c6ef; //$text-color--muted;
    font: italic normal 1.8rem/1.2 $font-family;
    text-align: center;
    padding-left: 1em;
    padding-right: 1em;
  }

  &__text a {
    color: #f1c6ef;
  }

  &__btn {
    text-transform: uppercase;
    letter-spacing: 0.1em;
    margin-left: auto;
    margin-right: auto;

    &:last-child {
      margin-bottom: 0;
    }

    // ЗАКРЫТЬ МОД.ОКНО
    &[data-dismiss='modal'] {
      opacity: 0.4;
      padding: calc(1.5rem - 2px) calc(3.5rem - 2px);
      transition: opacity 0.2s;

      &:hover {
        opacity: 1;
        box-shadow: none;
        background-color: $black;
        border-color: $black;
      }
    }
  }

  &__btn--on-md-down {
    @include media-breakpoint-up(lg) {
      & { display: none; }
    }
  }


  /* MQ
  ========================================= */
  @at-root {
    /* lg- */
    @include media-breakpoint-down(lg) {
      &__text {
        padding-left: 0;
        padding-right: 0;
      }
    } // End /lg
  } // End @at-root
}
