.place {
  padding-top: 26px;
  padding-bottom: $gap-v--section;
  overflow: hidden;

  &__grid {
    @include row();
    position: relative;
    align-items: flex-start;
    padding-top: 8rem;
    padding-bottom: 8.5rem;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: $grid-gutter-width / 2;
      left: $grid-gutter-width / 2;
      z-index: -2;
      width: auto;
      height: 100%;
      border: 3px solid $pink-lightest;
      border-radius: 20px;
    }
  }

  &__column {
    @include col-ready();
    @include col(7,4);
    text-align: left;
  }

  &__column-inner {
    margin-left: 70px;
  }


  &__title {
    margin-top: 2.5rem;
    text-align: left;
  }

  &__descr {
    margin-top: 3.2rem;
  }

  &__like {
    content: '';
    position: absolute;
    top: 0;
    left: -134px;
    z-index: -1;
    width: 100%;
    height: 100%;
    background: url('../img/like@1x.png') no-repeat 0 55%;
    user-select: none;
  }


  /* MQ
  ========================================= */
  @at-root {
    /* lg- */
    @include media-breakpoint-down(lg) {
      &__like {
        left: -208px;
      }
    } // End /lg

    /* md- */
    @include media-breakpoint-down(md) {
      &__column {
        @include col(10,1);
      }

      &__column-inner {
        margin-left: 0;
      }

      &__descr {
        margin-left: 33%;
      }

      &__like {
        left: -110px;
        background-size: 350px auto;
      }
    } // End /md

    /* sm- */
    @include media-breakpoint-down(sm) {
      &__column {
        margin-top: 200px;
        text-align: center;
      }

      &__descr {
        margin-left: 0;
      }

      &__grid {
        padding-bottom: 5rem;
      }

      &__like {
        top: 30px;
        left: 0;
        background-size: auto 250px;
        background-position: 40% top;
      }

      &__title {
        text-align: center;
      }
    } // End /sm

    /* xs */
    @include media-breakpoint-only(xs) {
      &__column {
        @include col(12,0);
        margin-top: 50vw;
      }

      &__like {
        top: 30px;
        left: 0;
        background-size: 70% auto;
        background-position: 25% top;
      }
    } // End /xs
  } // End @at-root
}
