.dream {
  position: relative;
  padding-right: 0;
  padding-left: 0;
  overflow: hidden;
  text-align: center;

  &::before,
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &::before {
    z-index: -2;
    background: #ffffff url('../img/bg_hero__wave@1x--pink.svg') repeat-x -190px 30px;
  }

  &::after {
    top: 150px;
    z-index: -1;
    background: #f9e7f3 url('../img/bg_dream.svg') no-repeat -300px 80px;
    background-size: 133% auto;
  }


  &__inner {
    padding-top: 240px;
    padding-bottom: 240px;
    overflow: hidden;
    background: url('../img/bg_hero__wave@1x.svg') repeat-x left -460px bottom -560px;
  }

  &__title {
    margin-top: 2.5rem;
  }

  &__content {
    @include row();
    margin-top: 50px;
  }

  &__slider {
    @include col-ready();
    @include col(10,1);
  }

  &__about {
    @include col-ready();
    @include col(10,1);
    margin-top: -5px;
  }

  &__yt-thumb {
    @include youtube-play-btn();
    position: relative;
    z-index: 1;
    text-align: center;
    background-color: $pink-light;
    border-radius: $border-radius--big;
    cursor: pointer;
  }

  &__yt-thumb a {
    display: inline-block;
    vertical-align: middle;
    width: 100%;
    height: 100%;
  }

  &__yt-thumb img {
    width: 975px;
    height: 548px;
    overflow: hidden;
    border-radius: $border-radius--big;

    object-fit: cover;
    object-position: 50% 50%;
  }

  &__scene-pencil {
    position: absolute;
    top: 48px;
    right: -220px;
    width: 30vw;
    height: 30vh;
  }

  &__pencil {
    width: 384px;
    height: 290px;
    background: url('../img/pensil@1x.png') no-repeat 0 0;
  }

  &__about-intro {
    font-weight: bold;
    font-size: 3rem;
    font-style: italic;
  }

  &__about-intro p + p {
    margin-top: 4rem;
  }

  &__grid-list {
    @include row();
    @include ie-li-nobullets();
    margin-top: 0.4rem;
    padding-right: 0;
    padding-left: 0; //$grid-gutter-width / 2; // 0
    text-align: left;
    counter-reset: list; /* counter */

    & > *:last-child:not(:nth-child(even)) {
      @include col(6,3);
    }
  }

  &__grid-list > li {
    @include col-ready();
    @include col(6);
    margin-top: 4.1rem;
    list-style: none;
  }

  &__list-item {
    position: relative;
    padding-right: $grid-gutter-width;
    padding-left: $grid-gutter-width;

    &::before {
      content: counter(list);
      position: absolute;
      top: 5px;
      left: -4rem;
      width: 54px;
      height: 54px;
      font-size: 3.6rem;
      line-height: 47px;
      text-align: center;
      border: 1px solid #ead0e1;
      border-radius: 50%;
      counter-increment: list;
    }
  }

  &__bottomline-wrap {
    @include row();
    margin-top: 5rem;
  }

  &__bottomline {
    @include col-ready();
    @include col(8,2);
  }


  /* MQ
  ========================================= */
  @at-root {
    /* lg- */
    @include media-breakpoint-down(lg) {
      &__list-item {
        margin-left: 15px;
      }

      .dream__scene-pencil {
        right: 20px;
      }
    } // End /lg

    /* md- */
    @include media-breakpoint-down(md) {
      &__about-intro {
        font-size: 2.5rem;
      }

      &__grid-list > li,
      &__grid-list > *:last-child:not(:nth-child(even)) {
        @include col(8,2);
      }

      &__list-item::before {
        left: -7rem;
      }

      .dream__scene-pencil {
        right: 0;
      }

      &__bottomline {
        @include col(10,1);
      }
    } // End /md

    /* sm- */
    @include media-breakpoint-down(sm) {
      // bg: волны
      &::before,
      &__inner {
        background-size: 1000px auto;
      }

      &__inner {
        padding-top: 80px;
        padding-bottom: 150px;
        background-position: left -50px bottom -90px;
      }

      &__grid-list > li,
      &__grid-list > *:last-child:not(:nth-child(even)) {
        @include col(10,1);
      }

      .dream__scene-pencil {
        left: 50%;
      }
    } // End /sm

    /* xs */
    @include media-breakpoint-only(xs) {
      &__about,
      &__grid-list > li,
      &__grid-list > *:last-child:not(:nth-child(even)),
      &__bottomline {
        @include col(12,0);
      }

      &__about-intro {
        font-size: 2rem;
      }

      .dream__scene-pencil {
        display: none;
      }

      &__list-item::before {
        position: relative;
        top: -5px;
        left: 0;
        display: block;
        margin-right: auto;
        margin-left: auto;
      }
    } // End /xs
  } // End @at-root
}
